'use client';

import { useState, useEffect } from 'react';
import { setContractAddress } from '@/lib/contract';
import TantoConnect from '@/components/TantoConnect';
import SamuraiCard from '@/components/SamuraiCard';
import BattleArena from '@/components/BattleArena';
import MessageDisplay from '@/components/MessageDisplay';

export default function Home() {
  const [connectedAddress, setConnectedAddress] = useState<string | null>(null);
  const [message, setMessage] = useState('');

  // Set contract address (you'll need to update this after deployment)
  useEffect(() => {
    // Replace with your deployed contract address
    setContractAddress(
      '0x0000000000000000000000000000000000000000' as `0x${string}`
    );
  }, []);

  const handleAddressChange = (address: string | null) => {
    setConnectedAddress(address);
  };

  const handleBalanceChange = () => {
    // Balance is handled within the TantoConnect component
  };

  const handleMessageChange = (newMessage: string) => {
    setMessage(newMessage);
  };

  if (!connectedAddress) {
    return (
      <TantoConnect
        onAddressChange={handleAddressChange}
        onBalanceChange={handleBalanceChange}
      />
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-900 via-red-800 to-red-900 p-4">
      <div className="max-w-6xl mx-auto">
        <TantoConnect
          onAddressChange={handleAddressChange}
          onBalanceChange={handleBalanceChange}
        />

        <MessageDisplay message={message} />

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <SamuraiCard onMessageChange={handleMessageChange} />
          <BattleArena
            connectedAddress={connectedAddress}
            onMessageChange={handleMessageChange}
          />
        </div>
      </div>
    </div>
  );
}
